<template lang="html">
  <div>
    <Navbar/>
    <Carousels/>
    <div>
      <v-container grid-list-xs,sm,md,lg,xl>
        <v-layout row wrap>
          <h1></h1>
        </v-layout>
        <v-card>
          <v-container grid-list-md>
            <v-layout wrap align-center>
              <v-flex xs12 sm6>
                <v-subheader v-text="''"></v-subheader>
              </v-flex>
              <v-flex xs12 sm6 md3>
               <v-dialog ref="dialog" v-model="modal" :return-value.sync="date" persistent lazy full-width width="290px">
                 <v-text-field slot="activator" v-model="date" label="///" prepend-icon="event" readonly></v-text-field>
                  <v-date-picker v-model="date" scrollable>
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="modal = false">Cancel</v-btn>
                    <v-btn flat color="primary" @click="$refs.dialog.save(date)">OK</v-btn>
                  </v-date-picker>
               </v-dialog>
              </v-flex>
              <v-flex xs12 sm6 md3>
               <v-select :items="times" v-model="selectTimes" label="" max-height="400"></v-select>
              </v-flex>
              <v-flex xs12 sm6>
               <v-subheader v-text="''"></v-subheader>
              </v-flex>
              <v-flex xs12 sm6>
               <v-textarea v-model="bio" color="primary"placeholder="    ">
               </v-textarea>
              </v-flex>
              <!-- <v-flex xs12 sm6>
                <v-subheader v-text="''"></v-subheader>
              </v-flex> -->
            </v-layout>
          </v-container>
        </v-card>
      </v-container>
      <v-container grid-list-xs,sm,md,lg,xl>
       <v-layout row wrap>
        <h1></h1>
       </v-layout>
       <v-card>
        <v-container fluid grid-list-xl>
         <v-form ref="form" v-model="valid" lazy-validation>
          <v-layout wrap align-center>
           <v-flex xs12 sm6>
            <v-subheader v-text="''"></v-subheader>
           </v-flex>
           <v-flex xs12 sm6>
            <v-text-field v-model="nameTH" :rules="NameRulesTH" placeholder="(/)" required></v-text-field>
           </v-flex>
           <v-flex xs12 sm6>
            <v-subheader v-text="''"></v-subheader>
           </v-flex>
           <v-flex xs12 sm6>
            <v-text-field v-model="surnameTH" :rules="SurnameRulesTH"></v-text-field>
           </v-flex>
           <v-flex xs12 sm6>
            <v-subheader v-text="''"></v-subheader>
           </v-flex>
           <v-flex xs12 sm6>
             <v-dialog ref="dialog2" v-model="modal2" :return-value.sync="date2" persistent lazy full-width width="290px">
              <v-text-field  slot="activator" v-model="date2" label="///" prepend-icon="event" :rules="[v => !!v || '']" readonly></v-text-field>
               <v-date-picker v-model="date2" scrollable>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="modal2 = false">Cancel</v-btn>
                <v-btn flat color="primary" @click="$refs.dialog2.save(date2)">OK</v-btn>
               </v-date-picker>
             </v-dialog>
           </v-flex>
           <v-flex xs12 sm6>
             <v-subheader v-text="''"></v-subheader>
           </v-flex>
           <v-flex xs12 sm6>
            <v-radio-group v-model="contactPerson" row>
             <v-radio class="mr-5" label="" value="newperson"></v-radio>
             <v-radio class="ml-5" label="" value="oldperson"></v-radio>
            </v-radio-group>
           </v-flex>
           <v-flex xs12 sm6>
            <v-subheader v-text="''"></v-subheader>
           </v-flex>
           <v-flex xs12 sm6>
             <v-text-field label="E-mail" v-model="email" :rules="emailRules" :error="chkEmailDuplicate" :error-messages="msgEmailDuplicate" color="primary" autocapitalize="off"></v-text-field>
           </v-flex>
           <v-flex xs12 sm6>
             <v-subheader v-text="''"></v-subheader>
           </v-flex>
           <v-flex xs12 sm6>
            <v-text-field v-model="phone" placeholder="" :rules="phoneRules" mask="###-###-####" max="10" required></v-text-field>
           </v-flex>
           <v-flex xs12 sm6 md12 offset-md5>
            <v-checkbox v-model="checkbox" :rules="[v => !!v || 'You must agree to continue']">
             <div slot="label">
              
              <a href="javascript:;" @click.stop="conditions = true"></a>
             </div>
            </v-checkbox>
           </v-flex>
           <v-dialog v-model="conditions" width="70%">
            <v-card>
              <v-card-text v-html="content"></v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="conditions = false">Ok</v-btn>
                </v-card-actions>
            </v-card>
           </v-dialog>
           <v-flex xs12 sm6 md12>
            <v-btn dark block style="background-color: #27417A;" @click="saveData" :loading="isLoading"></v-btn>
           </v-flex>
          </v-layout>
         </v-form>
        </v-container>
            <!-- <v-flex xs12 sm6>
             <v-subheader v-text="''"></v-subheader>
            </v-flex> -->
       </v-card>
      </v-container>
    </div>
   <Footer/>
 </div>
</template>

<script>
import Navbar from '@/components/center/Home/Navbar'
import Carousels from '@/components/center/Home/Carousels'
import Footer from '@/components/center/Home/Footer'
export default {
  components: {
    Navbar,
    Carousels,
    Footer
  },
  data () {
    return {
      bio: '',
      date: null,
      date2: null,
      modal: false,
      modal2: false,
      checkbox: false,
      conditions: false,
      valid: true,
      isLoading: false,
      nameTH: '',
      surnameTH: '',
      chkEmailDuplicate: false,
      msgEmailDuplicate: [],
      email: '',
      phone: '',
      NameRulesTH: [
        v => !!v || '',
        v => /^([- ])+$/.test(v) || ''
      ],
      SurnameRulesTH: [
        v => !!v || '',
        v => /^([- ])+$/.test(v) || ''
      ],
      emailRules: [
        v => !!v || '',
        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || ''
      ],
      phoneRules: [
        v => !!v || '',
        v => /^([0-9])+$/.test(v) || ''
      ],
      content: '<h4></h4>1. <br>&nbsp;&nbsp;&nbsp;1.1   <br>&nbsp;&nbsp;&nbsp;1.2       <br>&nbsp;&nbsp;&nbsp;1.3  <br><br>2.  One Platform  <br>&nbsp;&nbsp;&nbsp;2.1  One Platform   <br>&nbsp;&nbsp;&nbsp;2.2  One Platform    One Platform   One Platform  <br><br>3.  <br>&nbsp;&nbsp;&nbsp;3.1   One Platform  <br><br>4.  <br>&nbsp;&nbsp;&nbsp;4.1   <br>  <br>&nbsp;&nbsp;&nbsp;4.2  <br><br>5.  <br>&nbsp;&nbsp;&nbsp;5.1   <br>&nbsp;&nbsp;&nbsp;5.2  One Platform     One Platform          <br>',
      times: [
        '', '07:00-07:59', '08:00-08:59', '09:00-09:59', '10:00-10:59', '11:00-11:59',
        '12:00-12:59', '13:00-13:59', '14:00-14:59', '15:00-15:59', '16:00-16:59', '17:00-17:59'
      ],
      selectTimes: []
    }
  },
  methods: {
    saveData () {
      this.$swal({
        title: '',
        text: '',
        type: 'success'
      })
    },
    checkEmailDuplicate () {
      this.axios.get('https://one.th/api/check_email?email=' + this.email).then((response) => {
        if (response.data.result === 'Success') {
          this.chkEmailDuplicate = false
          this.msgEmailDuplicate = []
        }
      }, (error) => {
        console.log(error)
        this.chkEmailDuplicate = true
        this.msgEmailDuplicate = ['']
      })
      return true
    }
  },
  watch: {
    date () {
      if (this.date !== null) {
        var year = (parseInt(this.date.split('-')[0]) + 543).toString()
        this.dateShow = this.date.split('-')[2] + '-' + this.date.split('-')[1] + '-' + year
        this.dateThai = year + '-' + this.date.split('-')[1] + '-' + this.date.split('-')[2]
      }
    }
    // menu (val) {
    //   val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'))
    // }
  }
}
</script>

<style lang="css" scoped>
</style>



